




























.photo {
  border-radius: 50%;
  right: 1em;
  top: -1.15em;
  width: 10em;
}

@media screen and (orientation:portrait) and (max-width: 768px) {
  .photo {
    display: block;
    right: inherit;
    top: inherit;
    position: relative;
    margin-left: auto !important;
    margin-right: auto !important;
    width: 13em;
  }
}
