













































.row {
  display: flex;

  .col {
    margin: 0;
    margin-right: 0.25em;

    .eg-code-block.container {
      margin: 0;
      margin-bottom: 1em;
    }

    .code-box {
      width: 100%;
    }
  }
}

@media screen and (orientation:portrait) and (max-width: 768px) {
  .row {
    flex-direction: column;
  }
}
