

































h4 {
  margin-bottom: 0.5em;
  margin-top: 1.5em;
}

img {
  display: inline-block;
  padding-right: 0.75em;
  width: 1.75em;
}

p {
  color: #4183d7;
  font-style: italic;
  margin: 0;
  margin-bottom: 0.75em;
}

@media screen and (orientation:portrait) and (max-width: 768px) {
  h4, p {
    text-align: center
  }

  .box {
    align-items: flex-start;
    display: flex;
    justify-content: center;
  }
}
