



































.photo {
  right: 1em;
  top: 0em;
  width: 14em;
}

@media screen and (orientation: portrait) and (max-width: 768px) {
  .photo {
    display: block;
    right: inherit;
    top: inherit;
    position: relative;
    margin-left: auto !important;
    margin-right: auto !important;
    width: 17em;
  }
}
